import { Component, OnInit, OnDestroy, Renderer2, ElementRef, ViewChild } from '@angular/core';
import { trigger, state, style, animate, transition, query, stagger, keyframes, animateChild } from '@angular/animations';
import { Router } from '@angular/router';

import { SessionService } from './../../../core/services/session.service';
import { UserService } from './../../../core/services/user.service';
import { Subscription } from 'rxjs';
import { DomSanitizer } from '@angular/platform-browser';
import * as exifjs from 'exif-js';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
    selector: 'itx-dashboard-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss'],
    animations: [
        trigger('fade', [
            state('void', style({ opacity: 0 })),
            transition('void => *', animate('0.8s ease-out', style({ opacity: 1 })))
        ]),
        trigger('scale', [
            state('void', style({ transform: 'scale(0.5)', opacity: 0, transformOrigin: '50%' })),
            transition('void => *', animate('500ms ease', keyframes([
                style({ transform: 'scale(0.5)', opacity: 0, offset: 0 }),
                style({ transform: 'scale(1.1)', opacity: 0.8, offset: 0.8 }),
                style({ transform: 'scale(1)', opacity: 1, offset: 1 })
            ])))
        ]),
        trigger('animate', [
            transition('void => *', [query('@*', stagger(350, [animateChild()]), { optional: true })]),
        ])
    ]
})
export class HeaderComponent implements OnInit, OnDestroy {

    @ViewChild('renderImgEL') renderImgEL: ElementRef;

    public userInfos: any;
    public userPicture: any = null;
    public userConfig: any;
    private userInfosObs: Subscription;
    private userPictureSub: Subscription;

    public imgEl: any;
    public canvas: any;

    constructor(
        private userService: UserService,
        private router: Router,
        private sanitizer: DomSanitizer,
        private renderer: Renderer2,
        private elRef: ElementRef,
        private userConfigService: UserConfigService
    ) {
        this.userInfosObs = this.userService.userInfos$.subscribe(
            (res) => {
                this.userInfos = res;
                this.getPictureUser();
            },
            (err) => {
                console.log('Error retrieving UserInfo');
            }
        );
        this.userConfig = this.userConfigService.userconfig;
        this.userPictureSub = this.userService.userPic$.subscribe(res => {
            const newStyle = 'url(data:image/jpg;base64,' + res._body.replace(/"/g, '') + ')';
            this.userPicture = newStyle;

            //this.renderImage('data:image/jpg;base64,' + res._body.replace(/"/g, ''));
        });
    }

    ngOnInit(): void {
        this.userService.getUserInfos().subscribe();
    }

    ngOnDestroy(): void {
        this.userInfosObs.unsubscribe();
        this.userPictureSub.unsubscribe();
    }

    private renderImage(b64Img: any): void {
        this.imgEl = this.renderer.createElement('img');
        this.canvas = this.renderer.createElement('canvas');
        this.renderer.appendChild(this.renderImgEL.nativeElement.children[0], this.imgEl);
        this.renderer.appendChild(this.renderImgEL.nativeElement.children[0], this.canvas);

        this.imgEl.src = b64Img;
        this.renderImg(b64Img);
    }

    private renderImg(b64Img: any): void {
        const canvas = this.canvas;
        exifjs.getData(b64Img, function () {
            const allMetaData = exifjs.getAllTags(this);
            const exifOrientation: number = allMetaData.Orientation;
            const ctx = canvas.getContext('2d');
            const width = this.imgEl.width;
            const height = this.imgEl.height;

            if (
                exifOrientation === 5
                || exifOrientation === 6
                || exifOrientation === 7
                || exifOrientation === 8
            ) {
                canvas.width = height;
                canvas.height = width;
            } else {
                canvas.width = width;
                canvas.height = height;
            }

            // transform context before drawing image
            switch (exifOrientation) {
                case 2:
                    ctx.transform(-1, 0, 0, 1, width, 0);
                    break;
                case 3:
                    ctx.transform(-1, 0, 0, -1, width, height);
                    break;
                case 4:
                    ctx.transform(1, 0, 0, -1, 0, height);
                    break;
                case 5:
                    ctx.transform(0, 1, 1, 0, 0, 0);
                    break;
                case 6:
                    ctx.transform(0, 1, -1, 0, height, 0);
                    break;
                case 7:
                    ctx.transform(0, -1, -1, 0, height, width);
                    break;
                case 8:
                    ctx.transform(0, -1, 1, 0, 0, width);
                    break;
                default:
                    ctx.transform(1, 0, 0, 1, 0, 0);
            }

            // Draw img into canvas
            ctx.drawImage(this.imgEl, 0, 0, width, height);
        });
    }

    public uploadPicture(event): void {
        
        this.router.navigate(['', { outlets: { modal: 'upload-picture' } }]);
    }

    public goToProfile(): void {
        // this.router.navigate(['my-profile', { outlets: { modal: null } }]);
        this.router.navigate([{ outlets: { modal: null } }], { skipLocationChange: true, queryParams: { skipLocationChange: true } }).then(res => {
            this.router.navigate(['my-profile']);
        });
    }

    private getPictureUser(): void {
        this.userService.getPictureUser().subscribe();
    }

}
