import { Component, OnInit, OnDestroy } from '@angular/core';
import { trigger, state, style, animate, transition, query, stagger, keyframes, animateChild } from '@angular/animations';

import { RequestsService } from './../../../core/services/requests.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { LangService } from './../../../core/services/references/lang.service';
import { UserConfigService } from '@app/modules/core/services/references/user-configuration.service';

@Component({
  selector: 'itx-dashboard-page',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
  animations: [
    trigger('fade', [
      state('void', style({ opacity: 0 })),
      transition('void => *', animate('1s 750ms ease-out', style({ opacity: 1 })))
    ]),
    trigger('slide', [
      transition(
        ':enter',
        [animate('250ms ease-in-out', keyframes([
          style({ transform: 'translateY(80%)', opacity: 0, offset: 0 }),
          style({ transform: 'translateY(-10%)', opacity: 0.8, offset: 0.7 }),
          style({ transform: 'translateY(5%)', opacity: 1, offset: 0.9 }),
          style({ transform: 'translateY(0)', opacity: 1, offset: 1 })
        ]))]
      ),
      transition(
        ':leave',
        [animate('250ms ease-in-out', keyframes([
          style({ transform: 'translateY(0)', opacity: 1, offset: 0 }),
          style({ transform: 'translateY(80%)', opacity: 0, offset: 1 })
        ]))]
      )
    ]),
    trigger('showCard', [
      transition(':leave', [ query(':leave', stagger(50, [animateChild()]), { optional: true })]),
      transition(':enter', [ query(':enter', stagger(50, [animateChild()]), { optional: true })])
    ]),
  ]
})
// tslint:disable-next-line component-class-suffix
export class DashboardPage implements OnInit, OnDestroy {

  public translation;

  public helpdeskRequests: Array<any>;
  public allRequests: Array<any>;

  private helpdeskRequestsObs;
  private allOpenRequestsObs;
  public userConfig: any;

  constructor(
    private popupService: PopupService,
    private requestsService: RequestsService,
    private langService: LangService,
    private userConfigService: UserConfigService
  ) {
    this.translation = this.langService.lang;
    this.userConfig = this.userConfigService.userconfig;
    this.helpdeskRequests = [{}, {}, {}, {}, {}, {}, {}, {}, {}];

    this.allOpenRequestsObs = this.requestsService.allOpenRequests$.subscribe(res => {
      this.helpdeskRequests = res;
      this.helpdeskRequests.splice(9, this.helpdeskRequests.length - 9);
    });
  }

  ngOnInit(): void {
    const onboarding: string = localStorage.getItem('onboarding');

    if (onboarding === '1' || !onboarding) {
      const popupOptions: PopupOptions = {
        title: this.translation.dashboard.popIn.title,
        text: this.translation.dashboard.popIn.desc,
        bgState: false,
        position: {
            bottom: '14vh'
        }
      };
      this.popupService.setPopupState(true, popupOptions);
      localStorage.setItem('onboarding', '0');
    } else {
      // this.helpdeskRequests = [{}, {}, {}, {}, {}, {}, {}, {}, {}];
    }

    this.requestsService.getAllOpenRequests();
  }

  ngOnDestroy(): void {
    this.allOpenRequestsObs.unsubscribe();
  }

  public trackByRequest(index: number, request: any): number {
    return index;
  }

}
